import React, { useState, Fragment } from "react";

import Layout from "../components/Layout/layout";
import "./main.css";
import SearchInput from "../components/SearchInput";
import MainPage from "./Main Page";

const defaultCities = [{ name: "London" }, { name: "Paris" }, { name: "Berlin" }, { name: "ZÃ¼rich" }, { name: "Rome" }, { name: "Vienna" }];

function App() {
  const [searchValue, setSearchValue] = useState(defaultCities);

  const handleSearchSubmit = (value) => {
    if (value) {
      setSearchValue(getLocationObject(value));
    } else {
      setSearchValue(defaultCities);
    }
  };

  function getLocationObject(value) {
    return [{ name: value }];
  }

  return (
    <Layout>
      <Fragment>
        <SearchInput searchValue={searchValue} handleSearchSubmit={handleSearchSubmit} />
        <MainPage searchValue={searchValue} />
      </Fragment>
    </Layout>
  );
}

export default App;
